import React from 'react';
import { Link } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faRocket, faMeteor } from '@fortawesome/free-solid-svg-icons';
import Seo from '../components/seo';

const iconStyle = { maxHeight: '100%', marginRight: '1rem', marginLeft: '1rem' };

const NotFoundPage = () => (
  <>
    <Seo title="404" />
    <h1>
      <FontAwesomeIcon icon={faMeteor} style={iconStyle} />
      404
      <FontAwesomeIcon icon={faMeteor} style={iconStyle} />
    </h1>
    <p>Looks like you're lost in space.</p>
    <Link to={'/'}>
      <FontAwesomeIcon icon={faRocket} style={iconStyle} />
      Back to safety
    </Link>
  </>
);

export default NotFoundPage;